import common from "@/assets/js/common.js" 
export default {
data(){
    return {
        categoryList:[
            {
                icon:'icon-icon-test',
                name:'人物',
                active:true
            },{
                icon:'icon-huahui',
                name:'花卉',
                active:false
            },{
                icon:'icon-shanshui',
                name:'山水',
                active:false
            },{
                icon:'icon-huangguan',
                name:'吉瑞',
                active:false
            },{
                icon:'icon-dongwu',
                name:'动物',
                active:false
            },{
                icon:'icon-icon--',
                name:'字画',
                active:false
            },{
                icon:'icon-B-zhexuezongjiao',
                name:'宗教',
                active:false
            },
        ],
        secondtMenu:{}
    }
},
props:['arr'],
methods:{
    ...common,
      itemTap(index){
          for(let i in this.arr.list){
           this.arr.list[i].isActive='N'
       }
       this.arr.list[index].isActive='Y'
          this.$emit('getSecondId',this.arr.list[index].id);
          this.$store.commit('setSecondName',this.arr.list[index].fname)
      }
  }
}



