import common from "@/assets/js/common.js";
export default {
  data() {
    return {
      firstMenu: {}
    };
  },
  props:['arr'],
  mounted() {
    
  },
  methods: {
    ...common,
    itemTap(index) {
      console.log('执行了'+index)
       for(let i in this.arr.list){
           this.arr.list[i].isActive='N'
       }
       this.arr.list[index].isActive='Y'

       this.$emit('getNowFirst',this.arr.list[index].id)
    }
  }
};



