import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)

export  default new vuex.Store({
    // store对象
    state:{
        catalogId:'',
        secondName:''
    },
    mutations:{
        // 把二级分类的名字存起来
        setSecondName(state,n){
            state.secondName=n
        },
        // 把分类图片的id存起来
        setCategory(state,n){
            state.catalogId=n;
        }
    }
})


