import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

const common = {
  //    获取一级目录
  getFirstMenu() {
    var that = this;
    that.$post('/catalog/getFirstCatalogList', {})
      .then((res) => {
        let result = res.data.data;
        if (result.list.length != 0) {
          for (var i in result.list) {
            result.list[i].isActive = 'N'
          }
          result.list[0].isActive = 'Y';
          that.getSecondMenu(result.list[0].id)
        }

        that.firstMenu = result;
        // console.log('获取一级目录')
        // console.log(result)
        // console.log('默认点击第一个一级目录')

      })
  },
  //   获取二级目录
  getSecondMenu(id) {
    var that = this;
    that.$post('/catalog/querySecondCatelogList', {
        id: id
      })
      .then((res) => {
        let result = res.data.data;
        if (result.list.length != 0) {
          for (var i in result.list) {
            result.list[i].isActive = 'N'
          }
          result.list[0].isActive = 'Y';
          this.getCategory(result.list[0].id)
        }
        that.secondtMenu = result;
        // 第一次加载到恶时候把第一个名字存起来
        that.$store.commit('setSecondName',result.list[0].fname)
        // console.log('获取二级目录')
        // console.log(result)
      })
  },
  //   根据二级目录获取分类图
  getCategory(id) {
    // alert(1)
    var that = this;
    that.$post('/category/queryCategoryListByCatalogId', {
        catalogId: id
      })
      .then((res) => {
        let result = res.data.data;
        that.category = result;
        // console.log('获取分类图')
        // console.log(result)
      })
  },
  // 获取详情图片
  getAllDetailPic(id) {
    var that = this;
    that.$post('/product/getProductByCategoryIdList', {
        categoryId: id
      })
      .then((res) => {
        let result = res.data.data;
        if (result.list.length != 0) {
          var arr=[];
          for(var i in result.list){
            var obj={}
            obj.src=result.list[i].picUrl;
            obj.msrc=result.list[i].picUrl;
            arr.push(obj)
          }
          that.detailPic = arr;
        }
        // console.log('获取详情图片')
        //  console.log(arr)
      })
  }
}


export default common



