import fixText from "@/components/fixText";
import textLogo from "@/components/textLogo";
import secondCategory from "@/components/secondCategory";
import firstCategory from "@/components/firstCategory";
import common from "@/assets/js/common.js";
export default {
    // name: "category",

    data() {
        return {
            firstMenu: {},
            secondtMenu: [],
          
            category: {},
            firstMenuId: ""
        };
    },

    components: {
        secondCategory,
        fixText,
        firstCategory,
        textLogo
    },
    mounted() {
        this.getFirstMenu();
         
    },

    methods: {
        ...common,
        // 默认第一个一级目录被点击
        getNewFirstMenu(val){
            console.log('监听出来')
            console.log(val)
            this.firstMenu=val
        },
        // 获取点击的一级目录
        getNowFirst(id) {
            this.getSecondMenu(id);
        },
        // 点击二级目录获取分类图片
        getSecondId(id) {
            this.getCategory(id);
        },
        gotoSeeItems() {
            this.$router.push({
                path: "/categoryItems"
            });
        },
        gotoSeeDetailPic(id) {
          // alert(this.category.list[index].catalogId)
            this.$store.commit('setCategory',id);
            // console.log(id)
           this.$router.push({
                path: "/detailPic",
            });
        }
    }
};



